.t-check-in,
.t-check-out,
.t-datepicker {
    display: inline-block;
    position: relative;
    float: left
}

.t-datepicker {
    clear: both;
    width: 100%;
    font-size: 14px;
    line-height: 1.4em;
    max-width: 650px
}

.t-check-in,
.t-check-out {
    border-width: 1px;
    border-style: solid;
    width: 50%;
    box-sizing: border-box
}

.t-check-in .t-date-info-title,
.t-check-out .t-date-info-title {
    position: absolute;
    top: 12px;
    left: 33px;
    display: block;
    font-weight: 400;
    opacity: .5;
    font-size: 13px;
    cursor: pointer
}

.t-check-in .fa,
.t-check-out .fa {
    top: -1px;
    position: relative
}

.t-check-in {
    border-right-width: 1px;
    border-radius: 4px 0 0 4px
}

.t-picker-only {
    border-radius: 4px;
    width: 100%
}

.t-check-out {
    border-left-width: 0;
    border-radius: 0 4px 4px 0
}

.t-check-out .t-datepicker-day {
    left: -100%
}

.t-arrow-top {
    top: 32px;
    z-index: 9999
}

.t-arrow-top,
.t-arrow-top::after {
    border-width: 10px;
    border-style: solid;
    border-color: transparent transparent #ddd;
    display: inline-block;
    position: absolute
}

.t-arrow-top::after {
    top: -9px;
    left: -10px;
    content: '';
    border-width: 10px;
    border-bottom-color: #fff
}

.t-dates {
    padding: 10px 15px;
    height: 38px;
    box-sizing: border-box
}

.t-datepicker-day {
    border-width: 1px;
    border-style: solid;
    top: 51px;
    overflow: hidden;
    position: absolute;
    z-index: 9998;
    padding: 10px 0;
    border-radius: 4px;
    box-shadow: 0 7px 15px rgba(0, 0, 0, .25)
}

.t-table-wrap {
    width: 100%;
    padding: 0 10px;
    font-size: inherit;
    display: inline-block;
    vertical-align: top
}

.t-datepicker-days {
    width: 650px
}

.t-datepicker-days .t-table-wrap {
    padding: 0;
    width: 47%;
    margin-left: 2%
}

@media (max-width:480px) {
    .t-datepicker-days {
        width: 300px
    }
    .t-datepicker-days .t-table-wrap {
        margin-left: 0;
        width: 100%;
        padding: 0 10px
    }
}

@media (max-width:320px) {
    .t-datepicker-days {
        width: 290px
    }
    .t-datepicker-days .t-table-wrap {
        padding: 0 5px
    }
}

.t-table-condensed {
    width: 100%;
    border-spacing: 0;
    border-collapse: collapse;
    vertical-align: top
}

.t-next,
.t-prev,
.t-table-condensed td,
.t-table-condensed th {
    text-align: center;
    padding: 10px
}

.t-date-title {
    clear: both;
    width: 100%;
    text-align: center;
    display: inline-block;
    margin: 0;
    padding: 15px 0 10px
}

.t-day,
.t-disabled,
.t-end,
.t-range,
.t-start {
    border-width: 2px;
    border-style: solid
}

.t-arrow {
    border: none
}

.t-hover-day::after,
.t-special-day:before {
    content: '';
    border-style: solid
}

.t-arrow,
.t-dates,
.t-day,
.t-end,
.t-end-limit,
.t-range,
.t-start {
    cursor: pointer
}

.t-special-day {
    position: relative
}

.t-special-day:before {
    height: 3px;
    width: 3px;
    top: 0;
    right: 0;
    position: absolute;
    display: block;
    border-width: 3px;
    -webkit-box-sizing: content-box;
    -moz-box-sizing: content-box;
    box-sizing: content-box
}

.t-hover-day {
    position: relative
}

.t-hover-day-content {
    top: -30px;
    width: 70px;
    right: calc(50% - 35px);
    position: absolute;
    font-size: 12px;
    font-weight: 700;
    padding: 3px 5px;
    border-radius: 4px;
    z-index: 9999
}

.t-hover-day::after {
    position: absolute;
    top: -8px;
    right: calc(50% - 7px);
    border-width: 7px
}

.t-today .t-hover-day-content {
    z-index: 9998
}

.t-check-in .t-end-limit,
.t-disabled {
    opacity: .25;
    cursor: auto
}